}
.background-line::before {
  content: "";
  position: absolute;
  right: 1.3125rem;
  bottom: 37.1875rem;
  border-bottom: 1px solid #F9F871;
  transform-origin: bottom right;
  transform: rotate(61.09deg);
  width: 362.13333vw;
  z-index: -1;
}
@media screen and (min-width: 768px) {
  .background-line::before {
    transform: rotate(45deg);
    width: 141.25vw;
    bottom: 7.8125rem;
    right: 0;
    bottom: 7.8125rem;
  }
}
.background-line::after {
  content: "";
  position: absolute;
  right: 8.6875rem;
  bottom: 0;
  border-bottom: 1px solid #F9F871;
  transform-origin: bottom right;
  transform: rotate(-75.09deg);
  width: 550.13333vw;
  z-index: -1;
}
@media screen and (min-width: 768px) {
  .background-line::after {
    transform: rotate(-45deg);
    width: 141.25vw;
    right: 0;
  }
}

