  .page-top {
    right: 2rem;
    bottom: 2rem;
  }
}
.page-top > a {
  background: #fff;
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  transition: all 0.3s ease;
}
.page-top > a::before {
  content: "";
  width: 1.3125rem;
  height: 1.25rem;
  border: 0px;
  border-top: solid 2px #333;
  border-right: solid 2px #333;
  transform: rotate(-45deg) translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -0.25rem;
  margin-left: 0.25rem;
  transition: all 0.3s ease;
}
.page-top > a:visited {
  background: #fff;
}
.page-top > a:visited::before {
  border-color: #333;
}
.page-top > a:hover {
  background: #333;
  border-color: #fff;
}
.page-top > a:hover::before {
  border-color: #fff;
}
.page-top > a:active {
  background: #fff;
}
.page-top > a:active::before {
  border-color: #333;
}

