@use "global" as *;

.mv {
  position: relative;
  width: 100%;
  min-height: 100vh;
  
  &-content {
    position: absolute;
    content: "";
    display: block;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
    z-index: 100;
  }
  
  &__title{
    color: $white;
    line-height: 1;
    letter-spacing: rem(0.5);
    font-size: rem(28);
  
    @include mq('md'){
      font-size: rem(50);   
    }
  }
  
  &__text{
    color: $white;
    line-height: 1;
    letter-spacing: rem(0.5);
    margin-top: rem(24);
    font-size: rem(20);
    
    @include mq('md'){
      font-size: rem(25);  
    }
  }
}

